export default {
	data() {
		return {
			title: 'chooseLocation',
			hasLocation: false,
			location: {},
			locationAddress: ''
		}
	},
	methods: {
		chooseLocation: function () {
			uni.chooseLocation({
				success: (res) => {
					this.hasLocation = true,
						this.location = this.$commonJs.noYearFormatLocation(res.longitude, res.latitude),
						this.locationAddress = res.address
				}
			})
		},
		clear: function () {
			this.hasLocation = false
		}
	}
}
